import { makeStyles } from "@material-ui/core";
import axios from "axios";
import React, { useEffect, useState } from "react";
import useLocalStorage from "../../Hooks/useLocalStorage";

const useStyles = makeStyles((theme) => ({
  p: {
    margin: "0 0 13px 0",
    textAlign: "justify",
  },
  a: {
    color: "#fff",
  },
  center: {
    textAlign: "center",
    fontSize: "2rem",
  },
  container: {
    width: "95%",
    maxWidth: "1220px",
    margin: "0 auto",
  },
  entry: {
    display: "grid",
    gridTemplateColumns: "1fr 3fr",
    position: "relative",
  },
  entry__number: {
    fontSize: "6vw",
    fontWeight: "600",
    padding: "10px 0",
    position: "sticky",
    top: "0",
    textAlign: "center",
    height: "calc(10vw + 20px)",
    transition: "all 0.2s ease-in",
  },
  entry__content: {
    borderTop: "2px solid #28282b",
    display: "grid",
    gridTemplateColumns: "1fr 4fr",
    gridGap: "10px",
    padding: "15px 0",
    "&:title": {
      fontWeight: "600",
    },
    "&:story": {
      lineHeight: "26px",
    },
  },
}));

export default function Read() {
  const classes = useStyles();
  const [entry, setEntry] = useState([]);
  const [journal] = useLocalStorage("journal", "");

  useEffect(() => {
    axios.get(`/entry/all/${journal}`).then((response) => {
      setEntry(response.data);
    });
  }, []);
  return (
    <body>
      <div className={classes.container}>
        <h1 className={classes.center}>Journal Name</h1>
        {entry.map((e) => {
          return (
            <article className={classes.entry}>
              <div className={classes.entry__number}>{e.id}</div>
              <div className={classes.entry__content}>
                <div className={classes["&:title"]}>{e.created}</div>
                <div className={classes["&:story"]}>
                  <p className={classes.p}>{e.text}</p>
                </div>
              </div>
            </article>
          );
        })}
      </div>
    </body>
  );
}
