import { createContext, useContext } from "react";
import useLocalStorage from "../Hooks/useLocalStorage";

const JournalContext = createContext();

const JournalProvider = (props) => {
  const [journal, setJournal] = useLocalStorage("journal", "");
  return (
    <JournalContext.Provider value={{ journal, setJournal }}>
      {props.children}
    </JournalContext.Provider>
  );
};

export { JournalProvider };

const useJournal = () => {
  const { journal, setJournal } = useContext(JournalContext);
  return { journal, setJournal };
};
export default useJournal;
