import { Dialog, Fab, Grid, makeStyles } from "@material-ui/core";
import { Add } from "@material-ui/icons";
import axios from "axios";
import React, { useEffect, useState } from "react";
import Card from "../Components/Dashboard/Card";
import CreateCard from "../Components/Dashboard/CreateCard";
import useLocalStorage from "../Hooks/useLocalStorage";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    marginLeft: "5%",
    marginTop: "2%",
    marginRight: "5%",
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
  fab: {
    position: "absolute",
    bottom: theme.spacing(6),
    right: theme.spacing(6),
  },
}));

export default function Dashboard() {
  const classes = useStyles();
  const [createOpen, setCreateOpen] = useState(false);
  const [token] = useLocalStorage("token", "");
  const [journal, setJournal] = useState([]);
  useEffect(() => {
    axios
      .get(`/journal/all/${token}`)
      .then((response) => {
        console.log(response.data);
        setJournal(response.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);
  return (
    <>
      <div className={classes.root}>
        <Grid container spacing={5}>
          {journal.map((e) => {
            return (
              <Grid item xs={3}>
                <Card title={e.name} created={e.created} id={e.id} />
              </Grid>
            );
          })}
        </Grid>
        <Fab
          className={classes.fab}
          size="small"
          color="secondary"
          onClick={() => setCreateOpen(true)}
        >
          <Add />
        </Fab>

        <Dialog
          fullWidth={"md"}
          open={createOpen}
          onClose={() => setCreateOpen(false)}
        >
          <CreateCard close={() => setCreateOpen(false)} />
        </Dialog>
      </div>
    </>
  );
}
